import React, { Component } from "react";
import { IGameProps } from "./models/IGameProps";
import { IGameState } from "./models/IGameState";

class Game extends Component<IGameProps, IGameState> {
    constructor(props: IGameProps) {
        super(props);

        this.state = {
            answer: "",
            clues: []
        };
    }

    private createAnswer(length: number): void {
        let pool: string = "0123456789";
        let newAnswer: string = "";
        for (; length > 0; length--) {
            const char: string = pool[Math.floor(Math.random() * pool.length)];
            pool.replace(char, '');
            newAnswer += char;
        }

        this.setState({ answer: newAnswer });
    }

    public componentDidMount(): void {
        this.createAnswer(10);
    }

    public render(): JSX.Element {
        return (
            <React.Fragment>
                {this.state.answer}
            </React.Fragment>
        );
    }
}

export { Game };
